header {
	display: grid;
	grid-template-columns: -webkit-min-content 1fr 1fr -webkit-min-content;
	grid-template-columns: min-content 1fr 1fr min-content;
	grid-column-gap: 10px;
	-webkit-column-gap: 10px;
	        column-gap: 10px;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
	height: 15vh;

	font-family: "Sofia", cursive;
	color: white;
}

header h1 {
	margin: 0;
	word-wrap: none;
	white-space: nowrap;
}

.userServicesSection {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	width: 100%;
}

@media (max-width: 450px) {
	header {
		display: grid;
		grid-template-columns: -webkit-min-content 1fr;
		grid-template-columns: min-content 1fr;
		grid-template-rows: -webkit-min-content -webkit-min-content;
		grid-template-rows: min-content min-content;
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
		height: unset;
	}

	header .logo {
		grid-area: 1 / 2 / 1 / 2;
		grid-column-start: 1;
		grid-column-end: 2;
		grid-row-start: 1;
		grid-row-end: 1;
		justify-self: start;
		align-self: center;
	}

	header .title {
		grid-area: 1 / 2 / 1 / 3;
		justify-self: end;
		align-self: center;
	}

	header .username {
		grid-area: 2 / 1 / 2 / 2;
		justify-self: start;
		align-self: center;
		margin: 0px;
	}

	header .logout-button {
		grid-area: 2 / 2 / 2 / 3;
		justify-self: end;
		align-self: center;
	}

	header h3 {
		margin: 0px;
	}

	.profilePic {
		height: 30px;
	}
}

.username {
	display: inline-block;
	justify-self: end;
	word-wrap: none;
	white-space: nowrap;
	width: -webkit-min-content;
