.mat {
	display: flex;
	justify-content: space-evenly;
	align-items: center;

	height: 70vh;
	margin: 10px;

	background-color: rgba(0, 0, 0, 0.5);
}

@media only screen and (max-width: 1010px) {
	.mat {
		margin: 0;
	}
}

@media (max-height: 700px) {
	.mat {
		height: 65vh;
	}
}